<template>
  <div id="new-pet">
    <h3></h3>
    <div class="row">
    <form @submit.prevent="savePet" class="col s12">
      <div class="row">
        <div class="input-field col s12">
          <input type="text" v-model="pet_id" required>
          <label>Pet ID#</label>
        </div>
      </div>

           <div class="row">
        <div class="input-field col s12">
          <input type="text" v-model="type" required>
          <label></label>
        </div>
      </div>
        
        <div class="row">
        <div class="input-field col s12">
          <input type="text" v-model="name" required>
          <label></label>
        </div>
      </div>
            <div class="row">
        <div class="input-field col s12">
          <input type="text" v-model="breed" required>
          <label></label>
        </div>
      </div>
            <div class="row">
        <div class="input-field col s12">
          <input type="text" v-model="gender" required>
          <label></label>
        </div>
      </div>
            <div class="row">
        <div class="input-field col s12">
          <input type="text" v-model="age" required>
          <label></label>
        </div>
      </div>
            <div class="row">
        <div class="input-field col s12">
          <input type="text" v-model="color" required>
          <label></label>
        </div>
      </div>
      <div class="row">
        <div class="input-field col s12">
          <input type="text" v-model="weight" required>
          <label> (KG)</label>
        </div>
      </div>
      <div class="row">
        <div class="input-field col s12">
          <input type="text" v-model="contact" required>
          <label></label>
        </div>
      </div>
      <div class="row">
        <div class="input-field col s12">
          <input type="text" v-model="location" required>
          <label></label>
        </div>
      </div>
      <button type="submit" class="btn"></button>
      <router-link to="/" class="btn grey"></router-link>
    </form>
  </div>
  </div>
</template>

<script>
    import db from './firebaseInit'
    export default {
      name: 'new-pet',
      data () {
        return {
          pet_id: null,
          type: null,
          name: null,
          breed: null,
          gender: null,
          age: null,
          color: null,
          weight: null,
          contact: null,      
          location: null                 
        }
      },
      methods: {
        savePet () {
          db.collection('pets').add({
            pet_id: this.pet_id,
            type: this.type,
            name: this.name,
            breed: this.breed,
            gender: this.gender,
            age: this.age,
            color: this.color,
            weight: this.weight,
            contact: this.contact,
            location: this.location     
         })
          .then(docRef => {
            console.log(': ', docRef.id)
            this.$router.push('/')
          })
          .catch(error => {
            console.error(': ', error)
          })
        }
      }
    }
</script>



